header {
    width: 80%;
    margin: auto;
    padding-top: 4em;
    color: white;
}

#nav {
    position: relative;
}

#nav ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
}

#nav ul li {
    list-style: none;
}

.nav-title {
    font-size: 24px;
}

.nav-title i {
    margin-left: 10px;
    padding: 5px;
    border-radius: 10px;
    transition: 0.3s;
}

.nav-title i:hover {
    color: black;
    background-color: white;
}

.nav-title a {
    color: white;
}

.nav-hamburger {
    font-size: 24px;
    cursor: pointer;
}

.nav-links {
    font-size: 24px;
    display: none;
}

.nav-links a {
    color: #bdbdbd;
    margin-left: 10px;
    padding: 5px 12px;
    transition: 0.3s;
    border-radius: 8px;
}

.nav-link {
    color: white;
}

.nav-links a:hover {
    color: white;
}

@media (min-width: 768px) {
    .nav-hamburger {
        display: none;
    }

    .nav-links {
        display: flex;
    }
}
